  height: 100vh;
  position: absolute;
  left: 0;
  top: 0;
  overflow-y: scroll;
  overflow-x: hidden; }

h1 {
  font-size: 15vw; }

h2 {
  font-size: 4.45vw;
  font-weight: normal; }

h4 {
  font-size: 36px; }

p {
  font-size: 1.7em; }

a {
  text-decoration: none;
  color: unset; }
  a.anchor-classic {
    text-decoration: underline;
    font-weight: bold; }

ul {
  list-style: none; }

.txt-tachado {
  text-decoration: line-through; }

.header {
  line-height: 1; }
  .header h4, .header h1 {
    margin: 0; }

.menu-moving {
  position: fixed;
  right: 60px;
  top: 30px;
  z-index: 20; }
  .menu-moving ul {
    text-align: right; }

.section--wrapper {
  min-width: 100vw;
  min-height: 100vh;
  position: relative;
  overflow: hidden; }

.section--content-container {
  padding: 60px 120px;
  width: 100%; }

.section--background-container, .section--overlay-container {
  position: absolute;
  top: 0;
  left: 0; }

.section--background-container {
  z-index: -1;
  width: 100%;
  height: 100%; }

.section--overlay-container {
  z-index: 10; }

.the-section--landing {
  display: flex;
  align-items: center; }
  .the-section--landing-bouncy > div img {
    width: 227px;
    height: auto; }

.the-section--who-am-i {
  display: flex;
  align-items: center; }
  .the-section--who-am-i--lluvia {
    background: #FFF500;
    width: 100%;
    height: 100%; }
    .the-section--who-am-i--lluvia-nrml img {
      mix-blend-mode: normal; }
    .the-section--who-am-i--lluvia-scrn img {
      mix-blend-mode: screen; }
  .the-section--who-am-i .txt-tachado {
    position: relative; }
    .the-section--who-am-i .txt-tachado:after {
      content: 'Is it crass to say you do things for money?';
      display: flex;
      justify-content: center;
      align-items: center;
      padding: 15px;
      width: 165px;
      height: 82px;
      font-size: 12px;
      font-family: 'Courier New', Courier, monospace;
      background: #FFF500;
      position: absolute;
      color: #FFFFFF;
      top: -50px;
      left: 15px;
      opacity: 0;
      transform: rotate3d(1, 1, 1, 105deg);
      -webkit-transition: all 0.3s ease-in-out;
      -o-transition: all 0.3s ease-in-out;
      -moz-transition: all 0.3s ease-in-out;
      -ms-transition: all 0.3s ease-in-out;
      transition: all 0.3s ease-in-out; }
    .the-section--who-am-i .txt-tachado:hover {
      cursor: help; }
      .the-section--who-am-i .txt-tachado:hover:after {
        background: #000000;
        opacity: 1;
        transform: rotate3d(1, 1, 1, 0deg); }

.the-section--work {
  display: flex;
  align-items: center;
  color: #FF0000; }
  .the-section--work-list .artprj-link {
    position: relative;
    z-index: 5; }
    .the-section--work-list .artprj-link:after {
      content: '';
      display: block;
      width: 390px;
      height: 270px;
      left: -10%;
      top: -100px;
      opacity: 0;
      background-image: url("src/assets/empty-frame.png");
      background-position: center;
      background-size: contain;
      background-repeat: no-repeat;
      position: absolute;
      transform: scale(0);
      -webkit-transition: all 0.3s ease-in-out;
      -o-transition: all 0.3s ease-in-out;
      -moz-transition: all 0.3s ease-in-out;
      -ms-transition: all 0.3s ease-in-out;
      transition: all 0.3s ease-in-out; }
    .the-section--work-list .artprj-link:hover:after {
      opacity: 1;
      transform: scale(1); }
  .the-section--work-list .musicprj-link {
    position: relative; }
    .the-section--work-list .musicprj-link:after {
      content: '';
      display: block;
      width: 25vw;
      height: 65vh;
      left: 40%;
      bottom: -50%;
      opacity: 0;
      background-image: url("src/assets/me-dj.gif");
      background-position: center;
      background-size: cover;
      background-repeat: no-repeat;
      position: absolute; }
    .the-section--work-list .musicprj-link:hover:after {
      opacity: 1; }

.the-section--contact {
  display: flex;
  align-items: center;
  background: #2A19FF;
  color: #FFFFFF; }
  .the-section--contact-social {
    display: flex;
    padding: 0;
    margin: 30px 0; }
    .the-section--contact-social li {
      margin: 0 15px 0 0; }

.the-section--web-projects {
  background: #9F62DF;
  color: #FFFFFF; }

.the-section--music-projects h2 {
  margin: 0 0 15px 0; }

.the-section--music-projects p {
  margin: 0; }

.the-section--music-projects img {
  object-fit: cover;
  width: 100%;
  height: 100%; }
